<template>
    <div class="container" style="margin-top:20px;margin-bottom:20px">
        <h1>This is edit page</h1>
        
        <div class="" style="margin-bottom:20px">
            <!-- <form v-on:submit.prevent="updateTransaction"> -->
            <form v-on:submit.prevent="toggleModal()">
                <div class="form-group">
                    <label>Input Date:</label>
                    <input type="date" class="form-control" v-model="transaction.date" required>
                </div>
                <div class="form-group">
                    <label>Input Desc:</label>
                    <input class="form-control" v-model.lazy="transaction.desc" required>
                </div>
                <div class="form-group">
                    <label>Input Amount:</label>
                    <input type="number" class="form-control" v-model.lazy="transaction.amount" required>
                </div>
                <div class="form-group">
                  <label>Input Type:</label>
                  <select class="form-control" v-model.lazy="transaction.type.name" required>
                    <option value="" disabled selected>Please choose...</option>
                    <option v-for="item of allCategories" :key="item.id" v-bind:value="item.name">{{item.name}} {{signDisplay(item.sign)}}</option>
                  </select>
                </div>
                <div class="form-group">
                  <label>Input Method:</label>
                  <select class="form-control" v-model.lazy="transaction.method" required>
                    <option value="" disabled selected>Please choose...</option>
                    <option v-for="item of categories.methods" :key="item">{{item}}</option>
                  </select>
                </div>
                <div class="form-group">
                    <label>Input Tags:</label>
                    <input type="text" class="form-control" v-on:keydown="addTag">
                    <div>
                        <li v-for="item in transaction.tags" :key="item.id">{{item}}<span v-on:click="removeTag(transaction.tags.indexOf(item))"> (x) </span></li>
                    </div>  
                </div>
                <div class="form-group">
                    <label>Notes:</label>
                    <input class="form-control" v-model.lazy="transaction.notes">
                </div>
                <p>This transaction is editing by {{username}}</p>
                <p style="margin-top:15px;margin-bottom;15px">previous update : {{transaction.lastUpdate}}</p>
                <button type='submit' class="btn btn-primary" >Submit</button>
                <button type='button' class="btn btn-secondary" v-on:click.prevent="$router.go(-1)">Cancel</button>
                <button type='button' class="btn btn-danger" v-on:click.prevent="resetData">Reset</button>
            </form>
        </div>
        <div class="modal" tabindex="-1" id="submitConfirm">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title">Update Transaction</h5>
                <button type="button" class="close" v-on:click.prevent="closeModal()">
                  <span>&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <p> ?</p>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-primary" v-on:click.prevent="updateTransaction">Confirm</button>
                <button type="button" class="btn btn-secondary" v-on:click.prevent="closeModal()">Cancel</button>
              </div>
            </div>
          </div>
        </div>
        
        <!-- <div class="">
            <p>Preview:</p>
            <p>{{transaction}}</p>
            <p>{{currentVersion}}</p>
        </div> -->
    </div>
</template>
<script>

window.onclick = function(event) {
  let modal = document.getElementById('submitConfirm')
  if (event.target == modal) {
    modal.style.display = "none";
  }
}   

import {db} from './firebaseinit'
// import {eventBus} from '../main'

export default {

  props:['currentUser'],
  data(){
    return {
      username: this.currentUser,
      checkid: this.$route.params.id,
      transaction:{
        type:{
          name:'',
          sign:'',
        },
        notes:'',
      },
      currentVersion: [],
      categories:[],
      allCategories:[]
    }
  },
  methods:{
      updateTransaction(){
          console.log('You clicked submit')
          console.log(JSON.stringify(this.transaction))
          db.collection('Transaction').doc(this.transaction.id).update(
              {
                  date : new Date(this.transaction.date),
                  desc: this.transaction.desc,
                  amount: Number(this.transaction.amount),
                  type: {
                    name: this.transaction.type.name,
                    sign: this.signOf(this.transaction.type.name),
                  },
                  method: this.transaction.method,
                  tags: this.transaction.tags,
                  notes: this.transaction.notes,
                  lastUpdate: new Date(),
                  editedBy: this.username,
              })
          // eventBus.$emit('editData')
          alert(`your data is successfully updated`)
          this.$router.go('-1')
      },
    
      dateFormat(dateObj){
          let dd = ('0'+ dateObj.getDate()).slice(-2)
          let mm = ('0' + (dateObj.getMonth()+1)).slice(-2)
          let yy = dateObj.getFullYear()
          return `${yy}-${mm}-${dd}`
      },
      signOf(name){
        for(let i=0;i<this.allCategories.length;i++)
          if(this.allCategories[i].name===name){
            return this.allCategories[i].sign
          }
        return 'invalid'
      },
      addTag(e){
          if(e.target.value.length > 0 && (e.key === 'Enter' || e.key === ' ' || e.key === ',')){
              e.preventDefault()
              if(!this.transaction.tags.includes(e.target.value)){
                  console.log(`you just added a tag = ${e.target.value}`)
                  this.transaction.tags.push(e.target.value.trim())
                  e.target.value = ''
                  console.log(JSON.stringify(this.transaction.tags))
              }else{
                  alert(`your tag is already existed = ${e.target.value}`)
              }
          }
      },
      removeTag(index){
          console.log(`you just removed a tag = ${this.transaction.tags[index]} index = ${index}`)
          this.transaction.tags.splice(index,1)
          console.log(JSON.stringify(this.transaction.tags))
      },
      resetData(){
          this.transaction = JSON.parse(JSON.stringify(this.currentVersion))
      },
      signDisplay(sign){
        if(sign === 1){
          return '(+)'
        }else if(sign === -1){
          return '(-)'
        }else{
          return 'unidentified'
        }
      },
      //Modal Function
      toggleModal(){
        let modal = document.getElementById('submitConfirm')
        modal.style.display = 'block'
      },
      closeModal(){
        let modal = document.getElementById('submitConfirm')
        modal.style.display = 'none'
      }
  },
  created(){
    db.collection('Transaction').doc(this.checkid).get()
        .then((doc)=>{
            let item = {
                id: doc.id,
                date: this.dateFormat(doc.data().date.toDate()),
                desc: doc.data().desc,
                amount: doc.data().amount,
                type: doc.data().type,
                method: doc.data().method,
                tags: doc.data().tags,
                notes: doc.data().notes,
                lastUpdate: "",
            }
            if(doc.get('lastUpdate') != null){
                item.lastUpdate = doc.data().lastUpdate.toDate()
            }
            this.currentVersion = JSON.parse(JSON.stringify(item))
            this.transaction = JSON.parse(JSON.stringify(item))
            console.log(JSON.stringify(this.transaction))
        })
    db.collection('Categories').doc('Categories').get()
      .then((doc)=>{
        let item = {
            methods: doc.data().methods,
        }
        this.categories = JSON.parse(JSON.stringify(item))
      })
    db.collection('itemCategories').get()
    .then((querySnapshot)=>{
      querySnapshot.forEach((doc) => {
        let item={
          id: doc.id,
          name: doc.data().name,
          sign: doc.data().sign,
        }
        this.allCategories.push(item)
      })
    })
  }
}
</script>