<template>
    <div class="container" style="margin-top:20px;margin-bottom:20px">
      <h1>This is manage tags page</h1>
      
      <h5>Payment Method</h5>
      <div>
        <label>Input New Method:</label>
        <input type="text" class="form-control" v-on:keydown="addCat($event, updatedCat.methods)">
        <div>
          <li v-for="item in updatedCat.methods" :key="item">{{item}}<span v-on:click="removeCat(updatedCat.methods, item)"> (x) </span></li>
        </div>  
      </div>
      <h5>Tags</h5>
      <div>
        <label>Input New Tags:</label>
        <input type="text" class="form-control" v-on:keydown="addCat($event, updatedCat.tags)">
        <div>
          <li v-for="item in updatedCat.tags" :key="item">{{item}}<span> ({{}})</span><span v-on:click="removeCat(updatedCat.tags, item)"> (x) </span></li>
        </div>  
      </div>

      <p style="margin-top:15px;margin-bottom;15px">previous update : {{currentCat.lastUpdate}}</p>

      <button type='button' class="btn btn-primary" v-on:click.prevent="updateCat">Submit</button>
      <button type='button' class="btn btn-secondary" v-on:click.prevent="$router.go(-1)">Cancel</button>
      <button type='button' class="btn btn-danger" v-on:click.prevent="resetData">Reset</button>
    </div>
</template>
<script>

import {db} from './firebaseinit'

export default {

  data(){
    return{
      directions:['inward','outward'],
      currentCat:{
        types:[],
        methods:[],
        tags:[],
      },
      updatedCat:{
        types:[],
        methods:[],
        tags:[],
      },
      transactions:[]
    }
  },
  computed:{
    tagCount(){
      return null
    }
  },
  methods:{
    updateCat(){
      console.log('You clicked submit')
      db.collection('Categories').doc('Categories').update({
        types:this.updatedCat.types,
        methods:this.updatedCat.methods,
        tags:this.updatedCat.tags,
        lastUpdate: new Date()
      })
      alert(`your data is successfully updated`)
      this.$router.go('-1')
    },
    addCat(e, cat){
      if(e.target.value.length > 0 && (e.key === 'Enter' || e.key === ',')){
        e.preventDefault()
        if(!cat.includes(e.target.value)){
          console.log(`you just added a tag = ${e.target.value}`)
          cat.push(e.target.value.trim())
          e.target.value = ''
          console.log(JSON.stringify(cat))
        }else{
          alert(`your tag is already existed = ${e.target.value}`)
        }
      }
    },
    removeCat(cat, item){
      let index = cat.indexOf(item)
      console.log(`you just removed a tag = ${cat[index]} index = ${index}`)
      cat.splice(index,1)
      console.log(JSON.stringify(cat))
    },
    resetData(){
      this.updatedCat = JSON.parse(JSON.stringify(this.currentCat))
    },
    tagCount1(){
      console.log(`Tag counting ...`)
      console.log(JSON.stringify(this.updatedCat.tags))
      console.log(JSON.stringify(this.transactions))
      this.updatedCat.tags.forEach((tag)=>{
        let count = 0
        console.log(tag)
        this.transactions.forEach((item)=>{
          console.log(JSON.stringify(item))
          console.log(item.tags)
          if(item.tags.includes(tag)){
            count++
          }
        })
        console.log(`Tag [${tag}] is using by ${count} transactions`)
      })
    }
  },
  created(){
    db.collection('Categories').doc('Categories').get()
      .then((doc)=>{
          let item = {
              types: doc.data().types,
              methods: doc.data().methods,
              tags: doc.data().tags,
              lastUpdate: ""
          }
          if(doc.get('lastUpdate') != null){
              item.lastUpdate = doc.data().lastUpdate.toDate()
          }
          this.currentCat = JSON.parse(JSON.stringify(item))
          this.updatedCat = JSON.parse(JSON.stringify(item))
      })
    db.collection('Transaction').get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let item = {
            id: doc.id,
            date: doc.data().date.toDate(),
            desc: doc.data().desc,
            amount: doc.data().amount,
            direction: doc.data().direction,
            type: doc.data().type,
            method: doc.data().method,
            tags: doc.data().tags
          }
          this.transactions.push(item)
        })
      })
  }
}
</script>