<template>
    <div class="container" style="margin-top:20px;margin-bottom:20px">
      <h1>This is add categories page</h1>

      <div style="margin-top:30px;margin-bottom:30">
        <h5>Add a New Category</h5>
        <form v-on:submit="updateCat()">
          <div class="form-group">
            <label>Input new category's name</label>
            <input type="text" class="form-control" v-model="category.name" required>
          </div>
          <div class="form-group">
            <label>Nature of transaction</label>
            <select class="form-control" v-model="category.sign" required>
              <option value="" disabled selected>Please choose nature of transaction...</option>
              <option value="1">Inflow</option>
              <option value="-1">Outflow</option>
            </select>
          </div>
          <button type="submit" class="btn btn-primary">Submit</button>
          <button type='button' class="btn btn-danger" v-on:click.prevent="resetData">Reset</button>
        </form>
      </div>
      <div style="margin-top:30px;margin-bottom:30px">
        <h5>Categories List</h5>
        <table class="table">
          <thead>
            <th>Name</th>
            <th>Nature</th>
            <th>Delete</th>
          </thead>
          <tbody>
            <tr v-for="item of allCategories" :key="item.id">
              <td>{{item.name}}</td>
              <td>{{signDisplay(item.sign)}}</td>
              <td><button class="btn btn-danger" v-on:click.prevent="deleteCat(item.id)">delete</button></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
</template>
<script>

import {db} from './firebaseinit'

export default {

  data(){
    return{
      category:{
        name:'',
        sign:'',
      },
      defaultValue:{
        name:'',
        sign:'',
      },
      allCategories:[]
    }
  },
  methods:{
    updateCat(){
      console.log('You clicked submit')
      let check = this.allCategories.filter((item)=>{
        return item.name === this.category.name
      })
      if(check.length<1){
        db.collection('itemCategories').add({
          name:this.category.name,
          sign:Number(this.category.sign),
        })
        alert(`your data is successfully updated`)
        this.refreshData()
        // this.$router.go('-1')
      }else{
        console.log(`duplicate`)
      }
    },
    refreshData(){
      let temp = []
      db.collection('itemCategories').get()
      .then((querySnapshot)=>{
        querySnapshot.forEach((doc) => {
          let item={
            id: doc.id,
            name: doc.data().name,
            sign: doc.data().sign,
          }
          temp.push(item)
        })
      })
      this.allCategories = temp
    },
    deleteCat(id){
      console.log(`You clicked DELETE button of ${id}`)
      db.collection('itemCategories').doc(id).delete().then(()=> this.refreshData())
    },
    signDisplay(sign){
      if(sign === 1){
        return 'Inflow Transaction (+)'
      }else if(sign === -1){
        return 'Outflow Transaction (-)'
      }else{
        return 'unidentified'
      }
    },
    resetData(){
      this.category = JSON.parse(JSON.stringify(this.defaultValue))
    },
  },
  created(){
    db.collection('itemCategories').get()
      .then((querySnapshot)=>{
        querySnapshot.forEach((doc) => {
          let item={
            id: doc.id,
            name: doc.data().name,
            sign: doc.data().sign,
          }
          this.allCategories.push(item)
        })
        console.log(JSON.stringify(this.allCategories))
      })
  }
}
</script>