<template>
  <div class="container" style="margin-top:20px;margin-bottom:20px">
    <div class="col-lg-6 col-md-6 col-sm-8 col-10 mx-auto">
      <h5 style='text-align:center'>Log In</h5>
      <form v-on:submit.prevent = 'loginUser'>
        <div class="form-group">
            <label>Email: </label>
            <input type="email" class="form-control" v-model='email' required>
        </div>
        <div class="form-group">
            <label>Password:</label>
            <input type="password" class="form-control" v-model='password' required>
        </div>
        <button type='submit' class="btn btn-primary">Login</button>
      </form>
      <div style="margin-top:20px;margin-bottom:20px">No account ? Please <router-link to='/signup'>sign up</router-link></div>
    </div>
  </div>
</template>

<script>

import {auth} from './firebaseinit.js'

export default {
  data(){
    return{
      email: null,
      password: null
    }
  },
  methods:{
    loginUser(){
      auth.signInWithEmailAndPassword(this.email, this.password).catch(function(error) {
        // Handle Errors here.
        console.log(`Error = Code:${error.code}`)
        console.log(`${error.message}`)
        // ...
      }).then((user)=>{
        console.log(user)
        this.$router.replace('/')
      })
    }
  }
}
</script>