<template>
  <div class="container" style="margin-top:20px;margin-bottom:20px">
    
    <h1>HOME</h1>
    
    <div class="form-check">
      <input type="radio" class="form-check-input" id="monthDate" name="selectDate" v-model="dateChoice" value="1">
      <label for="monthDate" class="form-check-label"></label>
    </div>
    
    <div class="row" v-if="dateChoice==1">
      <div class="col">
        <div class="form-group">
          <select class="form-control" v-model="selectedMonth.year">
            <option v-for="year of oldYear" :key="year">{{year}}</option>
          </select>
        </div>
      </div>
      <div class="col">
        <div class="form-group">
          <select class="form-control" v-model="selectedMonth.month">
            <option v-for="month of monthArray" :key="month" v-bind:value="monthArray.indexOf(month)">{{month}}</option>
          </select>
        </div>
      </div>
    </div>
      
    <div class="form-check">
      <input type="radio" class="form-check-input" id="rangeDate" name="selectDate" v-model="dateChoice" value="2">
      <label for="rangeDate" class="form-check-label"></label>
    </div>
    
    <div class="row" v-if="dateChoice==2">
      <div class="col">
        <div class="form-group">
          <input class="form-control" type="date" v-on:input="selectedRange.start = $event.target.value">
        </div>
      </div>
      <div class="col">
        <div class="form-group">
          <input class="form-control" type="date" v-on:input="selectedRange.end = $event.target.value">
        </div>    
      </div>
    </div>
   
    <button type="button" style="margin-top:15px;margin-bottom:15px" class="btn btn-primary" v-on:click.prevent="submitDate()"></button>
    
    <div v-if="display == 1">
      <p> {{dateFormat(dateFilter.start)}}  {{dateFormat(dateFilter.end)}} </p>
      <!-- <p>Total Balance : {{sumAmount}} THB</p> -->
      <p>Filtered Balance : {{sumFiltered}} THB</p>
      <div>
        <div>Search Box</div>
        <input style="margin-top:15px;margin-bottom:15px" class="form-control" type="search" v-model="searchText" placeholder="search for transaction">
      </div>
      <p>Data List: <button style="float:right" type="button" class="btn btn-secondary" v-on:click.prevent="writeXlsx()">Export</button></p>

      <div class="card" v-for="item in filteredResult" :key="item.id">

        <div class="card-header">
          <!-- <a data-toggle="collapse" v-bind:href="`#collapse-${item.id}`"> -->
          <button class="btn btn-block p-0" data-toggle="collapse" v-bind:data-target="`#collapse-${item.id}`">
            <div class="d-flex flex-row flex-wrap justify-content-around">
              <div class="col-md-3 col-sm-3 col-12 p-0 text-left">{{dateFormat(item.date)}}</div>
              <div class="col-md col-sm col-8 p-0 text-left">{{item.desc}}</div>
              <div class="col-md-2 col-sm-3 col-4 p-0 pr-4 text-right" id="cell-amount">{{numberFormat(signDisplay(item))}}</div>
              <div class="col-md-3 col-12 p-0 text-left" id="cell-badge"><span class="badge badge-info" style="margin-right:5px" v-for="tag in item.tags" :key="tag">{{tag}}</span></div>
            </div>
          </button>
          <!-- </a> -->
        </div>
        
        <div class="collapse" v-bind:id="`collapse-${item.id}`">
          <div class="card-body">
            <div class="d-flex flex-row flex-wrap justify-content-around">
              <div class="col p-0">
                <div>Type: {{item.type.name}}</div>
                <div>Method: {{item.method}}</div>
              </div>
              <div class="col-4 p-0 text-right">
                <span><button class="btn btn-secondary" v-on:click="$router.push({path:`/view/${item.id}`})">view</button></span>
                <span><button class="btn btn-danger" v-on:click.prevent="deleteTransaction(item.id)">delete</button></span>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>

import {db} from './firebaseinit'
import XLSX from 'xlsx'
// import {eventBus} from '../main'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      transactions: [],
      searchText:'',
      monthArray:['JAN','FEB','MAR','APR','MAY','JUN','JUL','AUG','SEP','OCT','NOV','DEC'],
      selectedMonth:{
        year: new Date().getFullYear(),
        month: new Date().getMonth()
      },
      selectedRange:{
        start: null,
        end: null,
      },
      dateFilter:{
        start: new Date(2020,0,1),
        end: new Date(2021,0,0,23,59,59,999),
      },
      dateChoice:'1',
      display: 1,
      // startDate: new Date(2020,0,1),
      // endDate: new Date(new Date().setHours(23,59,59,999)),
      // thisYear: Number(new Date().getFullYear()),
    } 
  },
  computed:{
    oldYear(){
      let temp =[]
      for(let i=0;i<=2;i++){
        temp.push(this.selectedMonth.year-i)
      }
      return temp
    },
    dateMonth(){
      let temp ={
        start: new Date(this.selectedMonth.year,this.selectedMonth.month,1),
        end: new Date(this.selectedMonth.year,this.selectedMonth.month+1,0,23,59,59,999), 
      }
      return temp
    },
    dateRange(){
      return {
        start: new Date(new Date(this.selectedRange.start).setHours(0,0,0,0)),
        end: new Date(new Date(this.selectedRange.end).setHours(23,59,59,999)),
      }
    },
    sumAmount(){
      let temp = this.transactions
      let sum = 0
      for(let i=0;i<temp.length;i++){
        if(temp[i].type.sign !== ''){
          sum += this.signDisplay(temp[i])
        }else{
          console.log(`ERROR: Direction of ${i} = ${temp[i].type.sign}`)
        }
      }
      return this.numberFormat(sum)
    },
    sumFiltered(){
      let temp = this.filteredResult
      let sum = 0
      for(let i=0;i<temp.length;i++){
        if(temp[i].type.sign !== ''){
          sum += this.signDisplay(temp[i])
        }else{
          console.log(`ERROR: Direction = ${temp[i].type.sign}`)
        }
      }
      return this.numberFormat(sum)
    },
    filteredResult(){
      const x = this.transactions.filter((item)=>{
        let stringArray = []
        stringArray.push(item.desc)
        for(let tag of item.tags){
          stringArray.push(tag)
        }
        for(let x of stringArray){
          if(x.toUpperCase().match(this.searchText.toUpperCase())&&(item.date > this.dateFilter.start && item.date < this.dateFilter.end)){
            return true
          }
        }
        return false
      })
      return x
    }
  },
  methods:{
    deleteTransaction(id){
      console.log(`You clicked DELETE button of ${id}`)
      db.collection('Transaction').doc(id).delete().then(()=> this.getTransaction())
    },
    getTransaction(){
      console.log(`You called method : getTransaction ...`)
      let rawData = []
      db.collection('Transaction').get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
              let item = {
                id: doc.id,
                date: doc.data().date.toDate(),
                desc: doc.data().desc,
                amount: doc.data().amount,
                type: doc.data().type,
                method: doc.data().method,
                tags: doc.data().tags
              }
              rawData.push(item)
          })
          this.transactions = rawData.sort(this.compareDate)
          console.log(`@refresh`)
          console.log(JSON.stringify(this.transactions))
        })
    },
    compareDate(a,b){
      return a.date - b.date
    },
    dateFormat(dateObj){
      let dd = ('0'+ dateObj.getDate()).slice(-2)
      let mm = ('0' + (dateObj.getMonth()+1)).slice(-2)
      let yy = dateObj.getFullYear()
      return `${dd}/${mm}/${yy}`
    },
    numberFormat(num){
      return new Intl.NumberFormat('th-TH',{minimumFractionDigits: 2}).format(num)
    },
    signDisplay(transaction){
      return Number(transaction.amount)*(transaction.type.sign)
    },
    submitDate(){
      console.log(`@submitDate choice ${this.dateChoice}`)
      console.log(`Current time`)
      console.log(`Start = ${this.dateFilter.start}`)
      console.log(`End = ${this.dateFilter.end}`)
      let choice = []
      switch(this.dateChoice){
        case '1':
          choice = this.dateMonth
          break
        case '2':
          choice = this.dateRange  
          break
      }
      if(Object.prototype.toString.call(choice.start) === "[object Date]" && Object.prototype.toString.call(choice.end) === "[object Date]"){
        this.dateFilter = choice
        this.display = 1
      }else{
        this.display = 0
        alert(`Please enter correct date`)
        console.log(`Start = ${choice.start}`)
        console.log(`End = ${choice.end}`)
      }
      console.log(`New time`)
      console.log(`Start = ${this.dateFilter.start}`)
      console.log(`End = ${this.dateFilter.end}`)
      
    },
    writeXlsx(){
      const wb = XLSX.utils.book_new()
      const ws_data = []
      this.transactions.forEach((item)=>{
        let tagString = ''
        item.tags.forEach((tag)=>{
          if(item.tags.indexOf(tag)===0){
            tagString += tag
          }else{
            tagString += (','+tag)
          }
        })
        let temp = {
          Date : item.date,
          Desc: item.desc,
          Amount: item.amount,
          Type: item.type.name,
          Method: item.method,
          Tags: tagString
        }
        ws_data.push(temp)
      })
      console.log(JSON.stringify(ws_data))
      const ws = XLSX.utils.json_to_sheet(ws_data)
      XLSX.utils.book_append_sheet(wb,ws,'test')
      XLSX.writeFile(wb, "sheetjs.xlsx")
    }
  },
  created(){
      console.log(`First time downloading database ...`)
      let rawData = []
      db.collection('Transaction').get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            let item = {
              id: doc.id,
              date: doc.data().date.toDate(),
              desc: doc.data().desc,
              amount: doc.data().amount,
              type: doc.data().type,
              method: doc.data().method,
              tags: doc.data().tags
            }
            rawData.push(item)
          })
          this.transactions = rawData.sort(this.compareDate)
          // console.log(`@created`)
          // console.log(JSON.stringify(this.transactions))
        })
      // eventBus.$on('importData',this.getTransaction)
      // eventBus.$on('addData',this.getTransaction)
      
  }
}
</script>

