<template>
    <div class="container" style="margin-top:20px;margin-bottom:20px">
        <h1>This is add page</h1>
        <p>If you want to add multiple transactions <span><router-link to='/upload'> Upload </router-link></span></p>
        
        <div style="margin-top:30px;margin-bottom:30">
            <form id="myform" v-on:submit.prevent="toggleModal()">
                <div class="form-group">
                    <label>Input Date:</label>
                    <input type="date" class="form-control" v-model="newTransaction.date" required>
                </div>
                <div class="form-group">
                    <label>Input Desc:</label>
                    <input class="form-control" v-model.lazy="newTransaction.desc" required>
                </div>
                <div class="form-group">
                    <label>Input Amount:</label>
                    <input type="number" class="form-control" v-model.lazy="newTransaction.amount" required>
                </div>
                <div class="form-group">
                  <label>Input Type:</label>
                  <select class="form-control" v-model.lazy="newTransaction.type" required>
                    <option value="" disabled selected>Please choose...</option>
                    <option v-for="item of allCategories" :key="item.id" v-bind:value="item">{{item.name}} {{signDisplay(item.sign)}}</option>
                  </select>
                </div>
                <div class="form-group">
                  <label>Input Method:</label>
                  <select class="form-control" v-model.lazy="newTransaction.method" required>
                    <option value="" disabled selected>Please choose...</option>
                    <option v-for="item of categories.methods" :key="item">{{item}}</option>
                  </select>
                </div>
                <div class="form-group">
                    <label>Input Tags:</label>
                    <input type="text" class="form-control" v-on:keydown="addTag">
                    <div>
                        <li v-for="item in newTransaction.tags" :key="item.id">{{item}}<span v-on:click="removeTag(newTransaction.tags.indexOf(item))"> (x) </span></li>
                    </div>  
                </div>
                <div class="form-group">
                    <label>Notes:</label>
                    <input class="form-control" v-model.lazy="newTransaction.notes">
                </div>
                <p>This transaction is creating by {{username}}</p>
                <button type='submit' class="btn btn-primary">Submit</button>
                <button type='button' class="btn btn-secondary" v-on:click.prevent="$router.push('/')">Cancel</button>
                <button type='button' class="btn btn-danger" v-on:click.prevent="resetData">Reset</button>
            </form>
        </div>
        <div style="margin-top:30px;margin-bottom:30">
            <p>Preview:</p>
            <p>{{newTransaction}}</p>
        </div>
      <div class="modal" tabindex="-1" id="submitConfirm">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">Update Transaction</h5>
              <button type="button" class="close" v-on:click.prevent="closeModal()">
                <span>&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p> ?</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" v-on:click.prevent="addTransaction()">Confirm</button>
              <button type="button" class="btn btn-secondary" v-on:click.prevent="closeModal()">Cancel</button>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>

import {db} from './firebaseinit'

window.onclick = function(event) {
  let modal = document.getElementById('submitConfirm')
  if (event.target == modal) {
    modal.style.display = "none";
  }
}  

export default {
  props:['currentUser'],
  data(){
    return {
      username: this.currentUser,
      newTransaction:{
          date: this.dateFormat(new Date()),
          desc:"",
          amount:"",
          type:'',
          method:'',
          tags:[],
          notes:'',
      },
      templateTransaction:{
          date: this.dateFormat(new Date()),
          desc:"",
          amount:"",
          type:'',
          method:'',
          tags:[],
          notes:'',
      },
      categories:[],
      allCategories:[]
    }
  },
  methods:{
    addTransaction(){
      console.log('You clicked submit')
      db.collection('Transaction').add(
          {
              date : new Date(this.newTransaction.date),
              desc: this.newTransaction.desc,
              amount: Number(this.newTransaction.amount),
              type: this.newTransaction.type,
              method: this.newTransaction.method,
              tags: this.newTransaction.tags,
              notes: this.newTransaction.notes,
              lastUpdate: new Date(),
              createdBy: this.username,
          }).then((doc)=>{
            alert(`your data is successfully added to database`)
            // this.$router.push('/')
            this.$router.push({path:`/view/${doc.id}`})
          })
    },
        dateFormat(dateObj){
            let dd = ('0'+ dateObj.getDate()).slice(-2)
            let mm = ('0' + (dateObj.getMonth()+1)).slice(-2)
            let yy = dateObj.getFullYear()
            return `${yy}-${mm}-${dd}`
        },
        addTag(e){
            if(e.target.value.length > 0 && (e.key === 'Enter' || e.key === ' ' || e.key === ',')){
                e.preventDefault()
                if(!this.newTransaction.tags.includes(e.target.value)){
                    console.log(`you just added a tag = ${e.target.value}`)
                    this.newTransaction.tags.push(e.target.value.trim())
                    e.target.value = ''
                    console.log(JSON.stringify(this.newTransaction.tags))
                }else{
                    alert(`your tag is already existed = ${e.target.value}`)
                }
            }
        },
        removeTag(index){
            console.log(`you just removed a tag = ${this.newTransaction.tags[index]} index = ${index}`)
            this.newTransaction.tags.splice(index,1)
            console.log(JSON.stringify(this.newTransaction.tags))
        },
        resetData(){
            this.newTransaction = JSON.parse(JSON.stringify(this.templateTransaction))
        },
        signDisplay(sign){
          if(sign === 1){
            return '(+)'
          }else if(sign === -1){
            return '(-)'
          }else{
            return 'unidentified'
          }
        },
    //Modal Function
    toggleModal(){
      let modal = document.getElementById('submitConfirm')
      modal.style.display = 'block'
    },
    closeModal(){
      let modal = document.getElementById('submitConfirm')
      modal.style.display = 'none'
    }
  },
  created(){
    db.collection('Categories').doc('Categories').get()
      .then((doc)=>{
        let item = {
            types: doc.data().types,
            methods: doc.data().methods,
        }
        this.categories = JSON.parse(JSON.stringify(item))
      })
    db.collection('itemCategories').get()
      .then((querySnapshot)=>{
        querySnapshot.forEach((doc) => {
          let item={
            id: doc.id,
            name: doc.data().name,
            sign: doc.data().sign,
          }
          this.allCategories.push(item)
        })
      })
  }
}
</script>