<template>
    <div class="container" style="margin-top:20px;margin-bottom:20px">
        <h1>This is view page</h1>
        <p>your item:</p>
        <table class="table">
            <tbody>
                <tr>
                    <td>Date</td>
                    <td>{{dateFormat(transaction.date)}}</td>
                </tr>
                <tr><td>Description</td><td>{{transaction.desc}}</td></tr>
                <tr><td>Amount</td><td>{{numberFormat(transaction.amount)}}</td></tr>
                <tr><td>Type</td><td>{{transaction.type.name}}</td></tr>
                <tr><td>Sign</td><td>{{transaction.type.sign}}</td></tr>
                <tr><td>Method</td><td>{{transaction.method}}</td></tr>
                <tr><td>Tags</td><td><span class="badge badge-info" style="margin-right:5px" v-for="tag in transaction.tags" :key="tag">{{tag}}</span></td></tr>
                <tr><td>Notes</td><td>{{transaction.notes}}</td></tr>
                <tr><td>Created</td><td>{{transaction.createdBy}}</td></tr>
                <tr><td>Edited</td><td>{{transaction.editedBy}}</td></tr>
                <tr><td>Last update</td><td>{{transaction.lastUpdate}}</td></tr>
            </tbody>
        </table>
        <button class="btn btn-secondary" v-on:click="$router.push({path:`/view/${transaction.id}/edit`})">edit</button>
        <button class="btn btn-danger" v-on:click.prevent="toggleModal()">delete</button>
        <button class="btn btn-secondary" v-on:click="$router.push('/')">back to home</button>

        <div class="modal" tabindex="-1" id="deleteConfirm">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title">Delete Transaction</h5>
                <button type="button" class="close" v-on:click.prevent="closeModal()">
                  <span>&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <p> ?</p>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-primary" v-on:click.prevent="deleteTransaction()">Confirm</button>
                <button type="button" class="btn btn-secondary" v-on:click.prevent="closeModal()">Cancel</button>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>
<script>

window.onclick = function(event) {
  let modal = document.getElementById('deleteConfirm')
  if (event.target == modal) {
    modal.style.display = "none";
  }
}   

import {db} from './firebaseinit'
// import {eventBus} from '../main'

export default {

    data(){
        return {
            checkid: this.$route.params.id,
            transaction: {
                date: new Date(),
                type:{
                  name:'',
                  sign:'',
                }
            }
        }
    },
    methods:{
      deleteTransaction(){
        db.collection('Transaction').doc(this.transaction.id).delete()
          .then(()=> {
            alert(`your data is successfully deleted`)
            this.$router.push('/')
          })
      },
      dateFormat(dateObj){
          let dd = ('0'+ dateObj.getDate()).slice(-2)
          let mm = ('0' + (dateObj.getMonth()+1)).slice(-2)
          let yy = dateObj.getFullYear()
          return `${dd}/${mm}/${yy}`
      },
      numberFormat(num){
        return new Intl.NumberFormat('th-TH',{minimumFractionDigits: 2}).format(num)
      },
      //Modal Function
      toggleModal(){
        let modal = document.getElementById('deleteConfirm')
        modal.style.display = 'block'
      },
      closeModal(){
        let modal = document.getElementById('deleteConfirm')
        modal.style.display = 'none'
      }
    },
    created(){
        db.collection('Transaction').doc(this.checkid).get()
            .then((doc)=>{
                let item = {
                    id: doc.id,
                    date: doc.data().date.toDate(),
                    desc: doc.data().desc,
                    amount: doc.data().amount,
                    type: doc.data().type,
                    method: doc.data().method,
                    tags: doc.data().tags,
                    notes: doc.data().notes,
                    lastUpdate: doc.data().lastUpdate.toDate(),
                    createdBy: doc.data().createdBy,
                    editedBy: doc.data().editedBy,
                }
                this.transaction = item
            })
        // console.log(this.transaction)
    }
}
</script>