<template>
    <div class="container" style="margin-top:20px;margin-bottom:20px">
      <h5>This is manage admin page</h5>
      
      <div class="col-lg-8 col-md-10 col-sm-12 col-12 p-0 my-4">
        <h6>Admin List</h6>
        <div class="card" v-for="user in userList" :key="user.uuid">
          <div class='d-flex card-body py-1'>
            <div class="col-4 p-0 my-auto">
              {{user.username}}
            </div>
            <div class="col-4 p-0 my-auto">
              {{user.admin}}
            </div>
            <div class="col-4 p-0 my-auto text-right">
              <button v-if="!user.admin" class="btn btn-primary w-100" v-on:click.prevent="adminMake(user.username)">Make Admin</button>
              <button v-if="user.admin" class="btn btn-danger w-100" v-on:click.prevent="adminRemove(user.username)">Remove Admin</button>
            </div>
          </div>
        </div>
        
      </div>
    
    </div>
</template>
<script>

import {db} from './firebaseinit'

export default {

  data(){
    return{
      userList:[]
    }
  },
  computed:{
  },
  methods:{
    adminMake(user){
      db.collection('Users').doc(user).update({
        admin: true
      }).then(()=>{
        this.refreshList()
      })
    },
    adminRemove(user){
      db.collection('Users').doc(user).update({
        admin: false
      }).then(()=>{
        this.refreshList()
      })
    },
    refreshList(){
      this.userList = []
      db.collection('Users').get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let item = {
            username: doc.id,
            uuid: doc.data().uuid,
            email: doc.data().email,
            admin: doc.data().admin,
          }
          this.userList.push(item)
        })
      })
    }
  },
  created(){
    this.refreshList()
  }
}
</script>