import home from './components/Home.vue'
import add from './components/AddTransaction.vue'
import view from './components/ViewTransaction.vue'
import edit from './components/EditTransaction.vue'
import upload from './components/UploadXlsx.vue'
import cat from './components/ManageCategories.vue'
import tags from './components/ManageTags.vue'
import admin from './components/ManageAdmin.vue'
import login from './components/Login.vue'
import signup from './components/Signup.vue'

export default [
    {path:'/', component: home, meta:{requiresAuth: true}},
    {path:'/add', component: add, meta:{ requiresAuth: true}},
    {path:'/view/:id', component: view, meta:{ requiresAuth: true}},    
    {path:'/view/:id/edit', component: edit, meta:{ requiresAuth: true}},   
    {path:'/upload', component: upload, meta:{ requiresAuth: true}},
    {path:'/categories', component: cat, meta:{ requiresAuth: true, requiresAdmin: true}},
    {path:'/tags', component: tags, meta:{ requiresAuth: true, requiresAdmin: true}},
    {path:'/admin', component: admin, meta:{ requiresAuth: true, requiresAdmin: true}},
    {path:'/login', component: login},
    {path:'/signup', component: signup},
]