<template>
  <div class="container" style="margin-top:20px;margin-bottom:20px">
    
    <h1>New Transaction Upload</h1>
    
    <div style="margin-top:40px">
      <p>Please select file:</p>
      <input id="input-file" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel">
    </div>

    <div style="margin-top:40px">
      <h5>Current File Input: {{currentFile}}</h5><br>
      <p style="font-weight:bold">Data Preview:</p>
      <table class="table">
        <thead>
          <tr>
            <th>Date</th> 
            <th>Desc</th>
            <th>Amount (THB)</th>
            <th>Type</th>
            <th>Sign</th>
            <th>Method</th>
            <th>Tag</th>
            <th>Notes</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in jsonData" :key="item.date.toString()">
            <td>{{dateFormat(item.date)}}</td>
            <td>{{item.desc}}</td>
            <td style="text-align:right">{{numberFormat(item.amount)}}</td>
            <td>{{item.type.name}}</td>
            <td>{{item.type.sign}}</td>
            <td>{{item.method}}</td>
            <td><span v-for="tag in item.tags" :key="tag">{{tag}}</span></td>
            <td>{{item.notes}}</td>
          </tr>
        </tbody>
      </table>
      <button class="btn btn-primary" v-on:click.prevent="importData" :disabled="isDisabled">Import to Database</button>
      <button class="btn btn-secondary" v-on:click.prevent="resetData" :disabled="isDisabled">Reset</button>
    </div>
    
  </div>
</template>

<script>

import {db} from './firebaseinit'
import XLSX from 'xlsx'

export default {

  props:['currentUser'],
  data(){
    return {
      username: this.currentUser,
      temp:[],
      fileElement: [],
      currentFile: "",
      jsonData:[],
      isDisabled: true,
      allCategories:[]
    } 
  },
  methods:{
    excelToJsDate(eDate){
      return new Date((eDate-25569)*86400000)
    },
    dateFormat(dateObj){
      let dd = ('0'+ dateObj.getDate()).slice(-2)
      let mm = ('0' + (dateObj.getMonth()+1)).slice(-2)
      let yy = dateObj.getFullYear()
      return `${dd}/${mm}/${yy}`
    },
    numberFormat(num){
      return new Intl.NumberFormat('th-TH',{minimumFractionDigits: 2}).format(num)
    },
    signOf(name){
      for(let i=0;i<this.allCategories.length;i++)
        if(this.allCategories[i].name===name){
          return this.allCategories[i].sign
        }
      return 'invalid'
    },
    inputElement(){
      this.fileElement = document.getElementById('input-file')
      this.fileElement.addEventListener('change',this.handleFile)
    },
    handleFile(e){
      if(e.target.files.length>0){
        this.isDisabled = false
        console.log(`now isDisabled = ${this.isDisabled}`)
        const file = e.target.files[0]
        console.log(`you've selected a file = ${file.name}`)
        const reader = new FileReader()
        reader.onload = (e)=>{
          let temp = []
          let data = e.target.result
          let workbook = XLSX.read(data, {type:'binary'})
          let result = XLSX.utils.sheet_to_json(workbook.Sheets[workbook.SheetNames[0]],{defval:''})
          
          for(let i=0;i<result.length;i++){
            let item = {
              date: this.excelToJsDate(result[i].Date),
              desc: result[i].Desc,
              amount: result[i].Amount,
              type:{
                name: result[i].Type,
                sign: this.signOf(result[i].Type),
              },
              method: result[i].Method,
              tags: result[i].Tags.split(','),
              notes: result[i].Notes,
            }
            temp.push(item)
          }
          this.currentFile = file.name
          this.jsonData = temp
        }
        reader.readAsBinaryString(file)
      }else{
        console.log(`no file is selected`)
        console.log(`now isDisabled = ${this.isDisabled}`)
      }
    }
    ,importData(){
      if(this.jsonData.length>0){
        const temp = this.jsonData
        for(let i=0;i<temp.length;i++){
          db.collection('Transaction').add(
            {
              date: temp[i].date,
              desc: temp[i].desc,
              amount: temp[i].amount,
              type: {
                name: temp[i].type.name,
                sign: temp[i].type.sign,
              },
              method: temp[i].method,
              tags: temp[i].tags,
              notes: temp[i].notes,
              lastUpdate: new Date(),
              createdBy: this.username
            })
        }
        console.log(`your data is successfully imported to database`)
        this.jsonData = []
        document.getElementById('input-file').value = ""
        this.isDisabled = !this.isDisabled
        this.$router.push('/')
      }else{
        console.log(`your file doesn't contain valid data`)
      }
    }
    ,resetData(){
      console.log(`you clicked reset data`)
      this.jsonData = []
      document.getElementById('input-file').value = ""
      this.isDisabled = !this.isDisabled
      console.log(`now jsonData length = ${this.jsonData.length}`)
      console.log(`now isDisabled = ${this.isDisabled}`)
    }
  },
  created(){
    db.collection('itemCategories').get()
      .then((querySnapshot)=>{
        querySnapshot.forEach((doc) => {
          let item={
            id: doc.id,
            name: doc.data().name,
            sign: doc.data().sign,
          }
          this.allCategories.push(item)
        })
      })
  },
  mounted(){
    this.inputElement()
  }
}
</script>