<template>
  <div class="container" style="margin-top:20px;margin-bottom:20px">
    <div class="col-lg-6 col-md-6 col-sm-8 col-10 mx-auto">
      <h5 style='text-align:center'>Sign Up</h5>
      <form v-on:submit.prevent = 'signupUser'>
        <div class="form-group">
            <label>Username: </label>
            <input type="text" class="form-control" v-model='username' v-on:blur='checkUsername()' v-on:focus='()=>duplicateUsername=false' required>
            <div style='color:red' v-if='duplicateUsername'>Username '{{username}}' has already been used.</div>
        </div>
        <div class="form-group">
            <label>Email: </label>
            <input type="email" class="form-control" v-model='email' v-on:blur='checkEmail()' v-on:focus='()=>duplicateEmail=false' required>
            <div style='color:red' v-if='duplicateEmail'>Email '{{email}}' has already been used.</div>
        </div>
        <div class="form-group">
            <label>Password:</label>
            <input type="password" class="form-control" v-model='password' id='pwBox' minlength='6' required>
            <div class='form-check'>
              <input type="checkbox" class="form-check-input" id='showpass' v-on:click='showPassword()'>
              <label class="form-check-label" for='showpass'>show password</label>
            </div>
        </div>
        <button type='submit' class="btn btn-primary">Signup</button>
      </form>
      <div style="margin-top:20px;margin-bottom:20px">Back to <router-link to='/login'>Login</router-link></div>
    </div>
  </div>
</template>

<script>

import {db, auth} from './firebaseinit.js'

export default {
  data(){
    return{
      username: null,
      email: null,
      password: null,
      usernameList: [],
      emailList:[],
      duplicateUsername: null,
      duplicateEmail: null,
    }
  },
  methods:{
    checkUsername(){
      this.duplicateUsername = this.usernameList.includes(this.username)
    },
    checkEmail(){
      this.duplicateEmail = this.emailList.includes(this.email)
    },
    showPassword(){
      let x = document.getElementById('pwBox')
      if(x.type === 'password'){
        x.type = 'text'
      }else{
        x.type = 'password'
      }
    },
    signupUser(){
      if(!this.duplicateUsername && !this.duplicateEmail){
        auth.createUserWithEmailAndPassword(this.email, this.password).catch(function(error) {
          // Handle Errors here.
          console.log(`Error = Code:${error.code}`)
          console.log(`${error.message}`)
          // ...
        }).then(()=>{
          let temp={
            uuid: auth.currentUser.uid,
            username: this.username,
            email: this.email,
            password: this.password,
            admin: false,
          }
          db.collection('Users').doc(this.username).set(temp).then(()=>{
            console.log(temp)
            this.$router.replace('/')
          })
        })
      }
    }
  },
  created(){
    db.collection('Users').get().then((querySnapshot)=>{
      querySnapshot.forEach((doc)=>{
        this.usernameList.push(doc.id)
        this.emailList.push(doc.data().email)
      })
    })
  }
}
</script>