<template>
  <div>

    <div class="text-right">Login: {{isLoggedIn}} Admin: {{isAdmin}}</div>

    <nav class="navbar sticky-top navbar-expand-sm navbar-dark bg-success">
      <a class="navbar-brand" href="#">Personal FS</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#mainNav" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse justify-content-between" id="mainNav">
        <div class="navbar-nav">
          <router-link to='/' class="nav-item nav-link" v-if='isLoggedIn'>Home</router-link>
          <router-link to='/add' class="nav-item nav-link" v-if='isLoggedIn'>Add New</router-link>
          <router-link to='/categories' class="nav-item nav-link" v-if='isLoggedIn&&isAdmin'>Categories</router-link>
          <router-link to='/tags' class="nav-item nav-link" v-if='isLoggedIn&&isAdmin'>Tags</router-link>
          <router-link to='/admin' class="nav-item nav-link" v-if='isLoggedIn&&isAdmin'>Admin</router-link>
        </div>
        <div class="navbar-nav">
          <router-link to='/login' class="nav-item nav-link" v-if='!isLoggedIn'>Login</router-link>
          <router-link to='/signup' class="nav-item nav-link" v-if='!isLoggedIn'>Sign Up</router-link>
          <div class="nav-item dropdown" v-if='isLoggedIn'>
            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              User: {{username}}
            </a>
            <div class="dropdown-menu">
              <a href='#' class="dropdown-item" v-on:click.prevent='logout()'>Logout</a>
            </div>
          </div>
          
        </div>
      </div>
    </nav>

    <router-view v-bind:current-user="username"></router-view>

  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
// import UploadXlsx from './components/UploadXlsx.vue'
import {db, auth} from './components/firebaseinit'

export default {
  name: 'App',
  components: {
    // HelloWorld,
    // UploadXlsx
  },
  data(){
    return {
      username: null,
      isLoggedIn : null,
      isAdmin: null,
    }
  },
  methods:{
    logout(){
      console.log(`logged out`)
      auth.signOut().then(()=>{
        this.$router.replace('/login')
      })
    }
  },
  created(){
    auth.onAuthStateChanged((user)=>{
      if(user){
        console.log(`Already logged in`)
        console.log(`User: ${user.email}`)
        this.isLoggedIn = true
        db.collection('Users').where('uuid', '==' , user.uid).get().then((query)=>{
          query.forEach((doc)=>{
            this.isAdmin = doc.data().admin
            this.username = doc.id
          })
        })
      }else{
        console.log(`Please sign in`)
        this.isLoggedIn = false
      }
    })
  }
}
</script>

<style>
.modal{
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */  
}
</style>
